___CSS_LOADER_EXPORT___.push([module.id, "input[name=search]{background-image:url(/assets/images/magnifying-glass.svg);background-position:.8em;background-repeat:no-repeat;border-radius:4px;text-indent:1.2em}input[name=search].transition-border{border-bottom-left-radius:0;border-bottom-right-radius:0}.fade-enter-active{transition:opacity .5s}.fade-leave-active{transition:opacity 0s}.fade-enter,.fade-leave-to{opacity:0}", "",{"version":3,"sources":["webpack://./source/_assets/js/components/Search.vue"],"names":[],"mappings":"AA+IA,mBACA,yDAAA,CACA,wBAAA,CACA,2BAAA,CACA,iBAAA,CACA,iBACA,CAEA,qCACA,2BAAA,CACA,4BAGA,CAEA,mBACA,sBACA,CAEA,mBACA,qBACA,CAEA,2BAEA,SACA","sourcesContent":["<template>\n    <div class=\"flex flex-1 justify-end items-center text-right px-4\">\n        <div\n            class=\"absolute sm:relative w-full justify-end top-0 left-0 z-10 py-4 sm:py-0 px-4 sm:px-0 bg-gray-800\"\n            :class=\"{'hidden sm:flex': ! searching}\"\n        >\n            <label for=\"search\" class=\"hidden\">Search</label>\n\n            <input\n                id=\"search\"\n                v-model=\"query\"\n                ref=\"search\"\n                class=\"transition-fast h-12 w-full sm:w-1/2 sm:focus:w-3/4 bg-gray-900 shadow border border-gray-700 focus:border-teal-400 focus:bg-gray-200 outline-none cursor-pointer px-4 shadow-inner\"\n                :class=\"{ 'transition-border': query }\"\n                autocomplete=\"off\"\n                name=\"search\"\n                placeholder=\"Search\"\n                type=\"text\"\n                aria-label=\"Search the blog\"\n                @keyup.esc=\"reset\"\n                @blur=\"reset\"\n            >\n\n            <button\n                v-if=\"query || searching\"\n                class=\"absolute top-0 right-0 font-light text-3xl text-gray-500 hover:text-gray-600 focus:outline-none mt-4 sm:mt-0 pr-6 sm:pr-2\"\n                @click=\"reset\"\n            >&times;</button>\n\n            <transition name=\"fade\">\n                <div v-if=\"query\" class=\"absolute top-0 inset-x-0 sm:inset-auto w-full sm:w-1/2 text-left mb-4 mt-16 sm:mt-12\">\n                    <div class=\"flex flex-col bg-white border border-b-0 border-t-0 border-teal-400 shadow-lg mx-4 sm:mx-0 max-w-xs sm:max-w-sm lg:max-w-lg\" style=\"width: 520px;\">\n                        <a\n                            v-for=\"(result, index) in results\"\n                            class=\"bg-white hover:bg-gray-100 border-b border-teal-400 text-xl cursor-pointer p-4\"\n                            :class=\"{ 'rounded-b' : (index === results.length - 1) }\"\n                            :href=\"result.link\"\n                            :title=\"result.title\"\n                            :key=\"result.link\"\n                            @mousedown.prevent\n                        >\n                            {{ result.title }}\n\n                            <span class=\"block font-normal text-gray-600 text-sm my-1\" v-html=\"result.snippet\"></span>\n                        </a>\n\n                        <div\n                            v-if=\"! results.length\"\n                            class=\"bg-white w-full hover:bg-gray-100 border-b border-teal-400 rounded-b-lg shadow cursor-pointer p-4\"\n                        >\n                            <p class=\"my-0\">No results for <strong>{{ query }}</strong></p>\n                        </div>\n                    </div>\n                </div>\n            </transition>\n        </div>\n\n        <button\n            title=\"Start searching\"\n            type=\"button\"\n            class=\"flex sm:hidden justify-center items-center border rounded-full focus:outline-none h-10 px-3 bg-gray-900 border-gray-700\"\n            @click.prevent=\"showInput\"\n        >\n            <!--\n            Formerly <img src=\"/assets/images/magnifying-glass.svg\" alt=\"search icon\" class=\"h-4 w-4 max-w-none\">\n            -->\n            <svg\n                class=\"h-4 w-8\"\n                :class=\"{ 'text-white': !belongsToBlog }\"\n                width=\"13px\"\n                height=\"13px\"\n                viewBox=\"0 0 20 20\"\n                version=\"1.1\"\n                xmlns=\"http://www.w3.org/2000/svg\"\n                xmlns:xlink=\"http://www.w3.org/1999/xlink\"\n            >\n                <defs></defs>\n                <g stroke=\"none\" stroke-width=\"1\" fill=\"none\" fill-rule=\"evenodd\">\n                    <g\n                        transform=\"translate(-829.000000, -42.000000)\"\n                        :fill=\"svgFill\"\n                        fill-rule=\"nonzero\"\n                    >\n                        <path d=\"M843.319857,54.9056439 L848.707107,60.2928932 C849.097631,60.6834175 849.097631,61.3165825 848.707107,61.7071068 C848.316582,62.0976311 847.683418,62.0976311 847.292893,61.7071068 L841.905644,56.3198574 C840.55096,57.3729184 838.848711,58 837,58 C832.581722,58 829,54.418278 829,50 C829,45.581722 832.581722,42 837,42 C841.418278,42 845,45.581722 845,50 C845,51.8487115 844.372918,53.5509601 843.319857,54.9056439 Z M837,56 C840.313708,56 843,53.3137085 843,50 C843,46.6862915 840.313708,44 837,44 C833.686292,44 831,46.6862915 831,50 C831,53.3137085 833.686292,56 837,56 Z\" id=\"Mask\"></path>\n                    </g>\n                </g>\n            </svg>\n        </button>\n    </div>\n</template>\n\n<script>\nimport Fuse from 'fuse.js';\n\nexport default {\n    props: {\n        dataBelongsToBlog: {\n            type: String,\n            required: true,\n            default: '',\n        }\n    },\n    data() {\n        return {\n            fuse: null,\n            searching: false,\n            query: '',\n            belongsToBlog: this.dataBelongsToBlog === '1' || false,\n        };\n    },\n    computed: {\n        results() {\n            return this.query ? this.fuse.search(this.query) : [];\n        },\n        svgFill() {\n            return '#748294'; // text-gray-600\n        }\n    },\n    methods: {\n        showInput() {\n            this.searching = true;\n            this.$nextTick(() => {\n                this.$refs.search.focus();\n            })\n        },\n        reset() {\n            this.query = '';\n            this.searching = false;\n        },\n    },\n    created() {\n        axios('/index.json').then(response => {\n          console.log(response);\n            this.fuse = new Fuse(response.data, {\n                minMatchCharLength: 6,\n                keys: ['title', 'snippet', 'categories'],\n            });\n        });\n    },\n};\n</script>\n\n<style>\ninput[name='search'] {\n    background-image: url('/assets/images/magnifying-glass.svg');\n    background-position: 0.8em;\n    background-repeat: no-repeat;\n    border-radius: 4px;\n    text-indent: 1.2em;\n}\n\ninput[name='search'].transition-border {\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n    /*border-top-left-radius: .5rem;*/\n    /*border-top-right-radius: .5rem;*/\n}\n\n.fade-enter-active {\n    transition: opacity .5s;\n}\n\n.fade-leave-active {\n    transition: opacity 0s;\n}\n\n.fade-enter,\n.fade-leave-to {\n    opacity: 0;\n}\n</style>\n"],"sourceRoot":""}]);
